/*
	Sea foam: #87c3b9
	Dark sea foam: #3e7c72
	Light gray: #eeeeee
	Dark gray: #686868
	Mauve: #c3878c
  Background dark: #282c34
*/

.detailHeader {
  width: 100%;
}

.detailHeaderLeft {
  text-align: left;
}

.detailName {
  color: #87c3b9;
  font-size: calc(32px + 2vmin);
  margin-bottom: 8px;
}

.detailSubheading {
  font-size: calc(1px + 2vmin);
  color: #686868;
  margin-left: 6px;
}

.detailTagline {
  color: #87c3b9;
  font-size: calc(10px + 2vmin);
  margin: 0 auto;
  padding: 54px 14% 0 14%;
}
