@import url(https://fonts.googleapis.com/css?family=Gothic+A1);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
	Sea foam: #87c3b9
	Dark sea foam: #3e7c72
	Light gray: #eeeeee
	Dark gray: #686868 (104,104,104)
	Mauve: #c3878c
  Background dark: #282c34 (40,44,52)
*/

.Navbar {
  background-color: #282c34;
  background-color: rgba(104,104,104,0.8);
  border: none;
  margin: 0;
}

.Navbar-item {
  color: #eeeeee;
  font-size: calc(6px + 2vmin);
  margin: 5px 14px 0 14px;
  font-weight: normal;
}

.Navbar-item:active, .Navbar-item:hover {
  color: #87c3b9;
  text-decoration: none;
}

.navbar-nav{
    flex-direction: row;
}

.Nav-container {
  padding: 0 !important;
  margin: 0 auto !important;
}

/*
	Sea foam: #87c3b9
	Dark sea foam: #3e7c72
	Light gray: #eeeeee
	Dark gray: #686868
	Mauve: #c3878c
  Background dark: #282c34
*/

.Landing {
  background-color: white;
  color: #87c3b9;
  justify-content: center;
  padding-left: 10%;
  padding-right: 10%;
}

.Landing-tagline {
  font-size: calc(28px + 2vmin);
}

.nav-icon-container {
  height: 15vmin;
  margin: 2vmin;
  position: relative;
  display: inline-block;
}

.nav-icon {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 100%;
  border: 1px solid #87c3b9;
}

.nav-icon-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  opacity: 0;
  background-color: #c3878c;
  border: 1px solid #87c3b9;
  border-radius: 50%;
}
.nav-icon-text {
  color: white;
  font-size: calc(5px + 2vmin);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.nav-icon-container:hover .nav-icon-overlay {
  opacity: 0.8;
  cursor: pointer;
}

.Landing-link {
  color: #3e7c72 !important;
  font-weight: normal;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*
	Sea foam: #87c3b9
