  /* margin: 2vw; */
}

.Dev-mockup-desktop {
  /* width: 40vw; */
  height: 50vmin;
  /* margin: 2vw; */
}

.dev-overlay-container {
  /* height: 15vmin; */
  margin: 2vw;
  position: relative;
  display: inline-block;
}
.dev-overlay-phone {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 25% 6% 23% 6%;
  opacity: 0;
  background-color: #c3878c;
}
.dev-overlay-desktop {
